import React from 'react';
import PropTypes from 'prop-types';

const imageLinks = {
  ios: 'https://trello-attachments.s3.amazonaws.com/5e5f59df18fba4248e4d6126/426x128/249d3f87be16d0d0dcb3b66729f21dee/Apple_Store.png',
  android: 'https://trello-attachments.s3.amazonaws.com/5e5f59df18fba4248e4d6126/426x128/290bc4964f92d15546a2f387a7734daa/Google_Play.png',
};

class MobileStoreButton extends React.Component {
  static propTypes = {
    store: PropTypes.oneOf(['ios', 'android']).isRequired,
    url: PropTypes.string.isRequired,
    height: PropTypes.number,
    width: PropTypes.number,
    linkStyles: PropTypes.object,
    linkProps: PropTypes.object,
  };
  static defaultProps = {
    height: 75,
    width: 255,
  };

  render() {
    const {
      store,
      url,
      height,
      width,
      linkStyles,
      linkProps,
      ...props
    } = this.props;

    const defaultLinkStyles = {
      background: `url(${imageLinks[store]}) no-repeat`,
      backgroundSize: 'contain',
      display: 'inline-block',
      overflow: 'hidden',
      textDecoration: 'none',
      height: '100%',
      width: '100%', 
      marginRight: '10px',
      ...linkStyles,
    };

    return (
      <div style={{ height, width, marginRight: '10px', display: 'inline-block' }} {...props}>
        <a
          style={defaultLinkStyles}
          href={url}
          target="_blank"
          rel="noopener noreferrer"
          {...linkProps}
        >
          &nbsp;
        </a>
      </div>
    );
  }
}

export default MobileStoreButton;